import React from "react";
import "../styles/HotItem.css"

const HotItemCard = ({ image, name, price, index }) => {
  return (
    <div className="HotItemCard">
      <img src={image} alt={`${index} product`} />
      <p>{name}</p>
      <span>{price}</span>
    </div>
  );
};

export default HotItemCard;
